ng_panel{
  position: relative;
}
.idm_control_center_popover {
    .vc-sketch {
      padding: 0px;
      background: transparent;
      border-radius: 0px;
      box-shadow: none;
    }
    .vc-sketch-field .vc-input__input {
      background: transparent;
      width: 100%;
    }
  }
  .idm_control_center_colorpicker_icon {
    width: 16px;
    height: 16px;
  }
  
  .control-center-panel-select-ghost {
    opacity: 0.5;
    background: #d9d9d9 !important;
  }
  .control-center-panel-select-group,
  .control-center-panel-select-popover {
    padding: 0;
    margin: 0;
    .control-center-panel-select-group-item {
      list-style: none;
      display: inline-block;
      height: 32px;
      padding: 0 10px;
      line-height: 30px;
      font-size: 14px;
      border-radius: 4px;
      box-sizing: border-box;
      white-space: nowrap;
      cursor: move;
      margin-right: 10px;
      color: rgba(0, 0, 0, 0.65);
      background-color: #fff;
      border: 1px solid #d9d9d9;
      margin-bottom: 5px;
    }
    .control-center-panel-select-group-close {
      margin-left: 8px;
      cursor: pointer;
    }
    .control-center-panel-select-add {
      color: #40a9ff;
      background-color: #fff;
      border-color: #40a9ff;
      line-height: 30px;
    }
  }
  .control-center-panel-select-popover {
    max-width: 450px;
    .control-center-panel-select-group-item {
      margin-bottom: 10px;
      position: relative;
      cursor: pointer;
      transition: all linear 0.3s;
      &.select-active {
        opacity: 0.4;
      }
      &:hover{
        color: #40a9ff;
        border-color: #40a9ff;
      }
    }
    .control-center-panel-select-group-check {
      position: absolute;
      right: 2px;
      bottom: 2px;
      font-size: 8px;
      display: none;
    }
    .control-center-panel-select-footer {
      display: flex;
      justify-content: space-between;
      .control-center-panel-select-desc {
        font-size: 12px;
        color: #999;
      }
      & > *:last-child {
        text-align: right;
      }
    }
  }
  .control-setting-userselect-panel {
    display: flex;
    height: 300px;
    & > div {
      position: relative;
      height: 100%;
      display: flex;
      flex-direction: column;
      .right-selected-box {
        flex-grow: 2;
        overflow: auto;
      }
      .footer-check-number {
        right: 0px;
        bottom: 0px;
        font-size: 14px;
        color: #999;
        text-align: right;
        display: flex;
        height: 30px;
        line-height: 30px;
        background-color: #fff;
        justify-content: space-between;
        padding: 0px 10px;
        white-space: nowrap;
      }
    }
    & > div:first-child {
      width: 60%;
      border-right: 1px solid #d9d9d9;
      overflow: auto;
    }
    & > div:last-child {
      width: 40%;
    }
    .control-center-userdept-select-group {
      padding: 0;
      margin: 0;
      .control-center-userdept-select-group-item {
        display: block;
        height: 32px;
        padding: 0 30px 0px 10px;
        line-height: 32px;
        font-size: 14px;
        box-sizing: border-box;
        white-space: nowrap;
        cursor: move;
        color: rgba(0, 0, 0, 0.65);
        background-color: #fff;
        position: relative;
        width: 100%;
        text-overflow: ellipsis;
        overflow: hidden;
      }
      .control-center-userdept-select-group-close {
        margin-left: 8px;
        cursor: pointer;
        position: absolute;
        right: 10px;
        line-height: initial;
        top: 9px;
      }
    }
  }
  .idm-control-center-form{
    min-height: 100px;
  }
  .idm-control-center-form.ant-form-inline{
    .idm-control-center-slider,.idm-control-center-select{
      .ant-form-item-control-wrapper{
        min-width: 160px;
      }
    }
    .idm-control-center-treeSelect{
      .ant-form-item-control-wrapper{
        min-width: 200px;
      }
    }
    .idm-control-center-userSelect,.idm-control-center-codeSelect{
      .ant-form-item-control-wrapper{
        min-width: 250px;
      }
    }
  }
  .idm-control-center-userselect-dropdown{
    min-width: 450px;
  }
  .idm-control-center-bottom-fixed{
    position: fixed;
    &.env_develop{
        position: absolute;
    }
    left: 0px;
    right: 0px;
    padding: 10px;
    background-color: #ffffff;
    box-shadow: 0 0px 10px rgb(0 0 0 / 10%);
    z-index: 10;
    &.cc-btn-left{
      text-align: left;
    }
    &.cc-btn-center{
      text-align: center;
    }
    &.cc-btn-right{
      text-align: right;
    }
    &.cc-btn-position-fixedTop{
      top: 0px;
    }
    &.cc-btn-position-fixedBottom{
      bottom: 0px;
    }
  }
  .cc-btn-box-common{
    &>*{
      margin-left: 10px;
      &:first-child{
        margin-left: 0px;
      }
    }
    &.cc-btn-left{
      text-align: left;
    }
    &.cc-btn-center{
      text-align: center;
    }
    &.cc-btn-right{
      text-align: right;
    }
  }
